.plot {
  width: 1009px;
  height: 500px;
  margin-bottom: 10px;
}
.axis path {
  stroke: #ECF0F3;
  stroke-width: 1;
}
.axis text {
  fill: #99A5AD;
  font-family: Arial, sans-serif;
}
.line {
  fill: none;
  stroke-width: 2;
}
.highlight {
  stroke-width: 2;
  fill: white;
}

.highlight_line {
  stroke: #ECF0F3;
  stroke-width: 1;
}

.popup {
  font-family: Arial, sans-serif;
  font-size: 24px;
  position:  absolute;
  border-radius: 5px;
  background: white;
  box-shadow: 2px 2px 4px 4px #a9a9a9;
  z-index: 9999;
  padding: 20px 10px;

  left: 1100px;
  top: 10px;
  display: none;
}

.popup .row span:first-child {
  margin-right: 10px;
}

.select {
  margin: 0 0 20px 0;
  font-family: Arial, sans-serif;
  font-size: 24px;
}

.select input[type="checkbox"]{
  margin-right: 10px;
  cursor: pointer;
}
.select label {
  cursor: pointer;
}
